import { vue } from "@/main";
export const valueNotLesOne = (value) => {
    return value < 1 ? 1 : value;
};
export const scaleValue = (number, inMin, inMax, outMin, outMax, round = true) => {
    const val = (number - inMin) * (outMax - outMin) / (inMax - inMin) + outMin;
    if (round) {
        return Math.round(val);
    }
    return val;
};
export const timeInMinutes = (millis) => {
    var minutes = Math.floor(millis / 60000);
    var seconds = ((millis % 60000) / 1000).toFixed(0);
    // @ts-ignore
    return minutes + ":" + (seconds < 10 ? '0' : '') + seconds;
};
export const randomArrayElement = (arr) => {
    return arr[Math.floor(Math.random() * arr.length)];
};
