export class SpellPassive extends EntityWithCharacteristic {
    constructor(name, ownerId, type, timeAction = -1) {
        super();
        this.id = 0;
        this.name = name;
        this.ownerId = ownerId;
        this.type = type;
        this.timeAction = timeAction;
        this.init();
        const owner = this.getOwner();
        if (owner) {
            owner.addOrRemovePassiveSpell(this);
        }
    }
    getDescription() {
        return '';
    }
    getOwner() {
        return Game.game.getGameObjectById(this.ownerId);
    }
    //ÑÑÑÐµÐºÑ Ð¿Ð¾ÐºÐ° Ð¿Ð°ÑÑÐ¸Ð²ÐºÐ° Ð½Ð° Ð¿ÐµÑÑÐ¾Ð½Ð°Ð¶Ðµ
    init() {
    }
    effect() {
    }
    //ÑÑÑÐµÐºÑ Ð²Ð½Ð°ÑÐ°Ð»Ðµ ÑÐ¾Ð´Ð° Ð¿ÐµÑÑÐ¾Ð½Ð°Ð¶Ð° Ð¸ ÑÐ¼ÐµÐ½ÑÑÐµÐ½Ð¸Ðµ ÑÐ°Ð¹Ð¼ÐµÑÐ° Ð¿Ð°ÑÑÐ¸Ð²ÐºÐ¸
    applicationAndCompletion() {
        if (this.timeAction > 0) {
            this.effect();
            this.timeAction--;
        }
        if (this.timeAction === 0) {
            const owner = this.getOwner();
            if (owner) {
                owner.addOrRemovePassiveSpell(this, true);
            }
        }
    }
}
