import {GET_GAME, INIT_GAME} from "@/store/game";

export default {
  name: 'EventLog',
  data() {
    return {
      ops: {
        scrollPanel: {
          scrollingX: false,
        },
        vuescroll: {
          mode: 'native'
        }
      },
    }
  },
  mounted() {
    this.scrollDown();
  },
  methods: {
    scrollDown() {
      setTimeout(() => {
        if (this.$refs["vs"]) {
          this.$refs["vs"].scrollTo(
              {
                y: "100%"
              },
              1
          );
        }
      });
    },
  },
  computed: {
    gameMessages() {
      this.scrollDown();
      return this.$store.getters[GET_GAME].messages;
    },
  },
}
