import { Game } from "@/game/Game";
export class Zone {
    constructor(locations = []) {
        this.locations = [];
        Game.game.idsCounter++;
        this.id = Game.game.idsCounter;
        this.locations = locations;
        this.locations.forEach(row => {
            row.forEach(loc => {
                loc.setZone(this.id);
            });
        });
        Game.game.zones.push(this);
    }
}
