import { Game } from "@/game/Game";
import { CharacterBehaviorTypes } from "@/constants/constants";
import { vue } from "@/main";
//ÑÐ¾Ð´Ð¸ÑÐµÐ»Ñ Ð´Ð»Ñ Ð¿ÑÐµÐ´Ð¼ÐµÑÐ¾Ð² Ð¸ Ð°ÐºÑÐ¸Ð²Ð½ÑÑ ÑÐ¿Ð¾ÑÐ¾Ð±Ð½Ð¾ÑÑÐµÐ¹
export class Activ {
    constructor(name, ownerId, numberTargets = 1) {
        this.id = 0;
        this.name = name;
        this.ownerId = ownerId;
        this.numberTargets = numberTargets;
        Game.game.addSpellOrItemObject(this);
    }
    getDescription() {
        return '';
    }
    getOwner() {
        return Game.game.getGameObjectById(this.ownerId);
    }
    //Ð¾Ð¿ÑÐµÐ´ÐµÐ»ÐµÐ½Ð¸Ðµ Ð´Ð¾Ð¿ Ð¾Ð¿ÑÐ¸Ð¹ Ð´Ð»Ñ Ð´ÐµÐ¹ÑÑÐ²Ð¸Ñ Ð² Ð¸Ð½ÑÐµÑÑÐµÐ¹ÑÐµ
    initForAction(action) {
    }
    //Ð´Ð»Ñ Ð¸Ð³ÑÐ¾ÐºÐ° Ð² Ð¸Ð½ÑÐµÑÑÐµÐ¹ÑÐµ Ð´ÐµÐ¹ÑÑÐ²Ð¸Ð¹
    actionForInterface(targetId) {
        const actionForInterface = {
            name: this.name, targetId, action: () => {
            }
        };
        this.initForAction(actionForInterface);
        actionForInterface.action = () => {
            if (!actionForInterface.disabled) {
                if (this.numberTargets > 1) {
                    const target = Game.game.getGameObjectById(actionForInterface.targetId);
                    if (target?.locationId) {
                        const location = Game.game.getLocation(target.locationId);
                        if (location) {
                            const objs = Game.game.getLocationCharacters(location);
                            if (objs) {
                                Game.game.modalSelectTargets = {
                                    action: (targetId2) => this.action(targetId2),
                                    actionPostEffects: () => this.actionInterfacePostEffects(),
                                    objs,
                                    location,
                                    numberTargetsNeeded: this.numberTargets,
                                };
                            }
                        }
                    }
                }
                else {
                    this.action(actionForInterface.targetId);
                    this.actionInterfacePostEffects();
                }
            }
        };
        return actionForInterface;
    }
    //Ð¿Ð¾ÑÑÑÑÑÐµÐºÑ Ð¿Ð¾ÑÐ»Ðµ Ð¿ÑÐ¸Ð¼ÐµÐ½ÐµÐ½Ð¸Ñ ÑÐ¿ÐµÐ»Ð°(Ð² ÑÐ¾Ð¼ ÑÐ¸ÑÐ»Ðµ Ð¿Ð¾ Ð½ÐµÑÐº ÑÐµÐ»ÑÐ¼) Ð² Ð¸Ð½ÑÐµÑÑÐµÐ¹ÑÐµ
    //(ÑÐ¼ÐµÐ½ÑÑÐ°ÐµÐ¼ Ð¾ÑÐºÐ¸ Ð´ÐµÐ¹ÑÐ²ÑÑÐ¼ Ð³ÐµÑÐ¾Ñ Ð¸Ð³ÑÐ¾ÐºÐ°, Ð½Ð¾ Ð¾Ð±ÑÑÐ½Ð¾ Ð¿ÑÐµÐ´Ð¼ÐµÑÑ Ð½Ðµ ÑÐ¼ÐµÐ½ÑÑÐ°ÑÑ ÑÐºÑÐµÐ½ Ð¿Ð¾Ð¸Ð½ÑÑÑ
    // Ð·Ð°ÐºÑÑÐ²Ð°ÐµÐ¼ Ð¼Ð¾Ð´Ð°Ð»ÐºÐ¸
    //!! Ð¿ÑÐµÐ´Ð¼ÐµÑÑ Ð½Ðµ ÑÐ°ÑÑÐ¾Ð´ÑÑÑ Ð¾ÑÐºÐ¸ Ð´ÐµÐ¹ÑÑÐ²Ð¸Ð¹!
    actionInterfacePostEffects(decreaseActionPoints = true) {
        const owner = this.getOwner();
        if (owner) {
            if (decreaseActionPoints) {
                owner.actionsPoints--;
            }
            Game.game.closeAllModals();
            Game.game.endHeroTurnWithCheckEndPoints();
        }
    }
    action(targetId) {
        const owner = this.getOwner();
        //ÐµÑÐ»Ð¸ Ð½Ð¿Ñ, ÑÐ¾ ÑÐ¼ÐµÐ½ÑÑÐ°ÐµÐ¼ ÐºÐ¾Ð»-Ð²Ð¾ ÐµÐ³Ð¾ Ð´ÐµÐ¹ÑÑÐ²Ð¸Ð¹ ÑÑÑ
        if (owner.behaviorType !== CharacterBehaviorTypes.Hero) {
            owner.actionsPoints--;
        }
        const target = Game.game.getGameObjectById(targetId);
        if (target) {
            Game.game.addMessage(vue.$t('character_use_activ', {
                activ: vue.$t(this.name),
                owner: vue.$t(owner.name),
                target: vue.$t(target.name)
            }));
        }
    }
}
